import './index.scss';
import template from './index.html';

import 'components/base/head-menu';
import G from 'constants';
import DeviceService from 'services/device';

export default {
	template,
	data() {
		return {
			G,
			currentIndex: 0, //当前页面的索引
			offset: 0, // 偏移量，由currentIndex计算而得
			headMenuClassName: ''
		};
	},

	created() {
		// document.addEventListener('scroll', e => {
		// 	e.preventDefault();
		// 	this.headMenuClassName = document.body.scrollTop > 200 ? 'wheel' : '';
		// });

		// 总页数
		this.sectionMount = 2;

		if (DeviceService.isMobile) {
			// 禁止页面在顶部和底部可以被拉动，影响touchstart事件的执行效果
			window.ontouchmove = document.ontouchmove = e => {
				return false;
			};

			const windowHeight = window.screen.height;
			document.addEventListener('touchstart', evt => {
				const startY = evt.touches[0].pageY;

				// 使用节流和防抖动函数会与touchend事件冲突（touchend被先执行）
				// let offsetYY;
				// const translateTo = () => { this.translateTo(offsetYY / windowHeight * 100); };
				// const debounceTranslateTo = debounce(10, translateTo);
				// const throttleTranslateTo = throttle(debounceTranslateTo, this);

				const touchmove = e => {
					const offsetYY = e.changedTouches[0].pageY - startY;
					this.translateTo(offsetYY / windowHeight * 100);
					// throttleTranslateTo();
				};

				// touchend
				const touchend = e => {
					document.removeEventListener('touchend', touchmove, false);
					document.removeEventListener('touchend', touchend, false);
					const offsetY = e.changedTouches[0].pageY - startY;

					// 手指滑动距离太小
					if (Math.abs(offsetY) < 100) return this.switchTo(this.currentIndex);

					return wheel(offsetY > 0, 0);
				};

				document.addEventListener('touchmove', touchmove, false);
				document.addEventListener('touchend', touchend, false);

				return false;
			}, false);
		} else {
			window.onwheel = document.onwheel = e => {
				return wheel(e.wheelDelta > 0);
			};
		}


		/**
		 * 当鼠标滚动（PC设备）或上下滑动（Mobile设备）时切换页面
		 * @param  {boolean} dirrection [true代表向上,false代表向下]
		 */
		const wheel = (dirrection, delay = 800) => {
			if (delay) { // 阻止函数多次执行，200ms内仅执行一次
				if (this.wheelTimerID) return false;

				this.wheelTimerID = window.setTimeout(() => {
					window.clearTimeout(this.wheelTimerID);
					this.wheelTimerID = undefined;
				}, delay);
			}

			// 向上滚动
			if (dirrection) {
				// 如果已经是第一页，则不响应
				if (this.currentIndex === 0)
					return this.switchTo(this.currentIndex);

				this.switchTo(this.currentIndex - 1);
			} else {
				if (this.currentIndex === this.sectionMount - 1)
					return this.switchTo(this.currentIndex);

				this.switchTo(this.currentIndex + 1);
			}
			return false;
		};
	},

	methods: {
		// 切换至第N页
		switchTo(index) {
			this.currentIndex = index;
			this.offset = 'transition: transform 1s ease-in-out;transform:translateY(' + (index * -100) + 'vh)';
		},
		translateTo(offset) {
			this.offset = 'transform:translateY(' + (this.currentIndex * -100 + offset) + 'vh)';
		}
	}
};

